import React, { Component } from 'react';
import SmallCard from './SmallCard';


class ContentRowTop extends Component {
    constructor(props) {
        super(props)
        this.state = {
            cartProps: []
        }
    }

    updateInfo() {
        fetch('http://localhost:3000/api/actors')
            .then(resp => resp.json())
            .then(result =>
                this.setState({
                    cartProps: [{
                        title: 'Cantidad de actores',
                        color: 'primary',
                        cuantity: result.count,
                        icon: 'fa-clipboard-list'

                    }]
                })
            )
    }

    componentDidMount() {
        this.updateInfo()
    }

    render() {
        return (

            <div className="row" >
                {
                    this.state.cartProps.map((product, i) => {

                        return <SmallCard {...product} key={i} />

                    })
                }

            </div>
        )
    }

}

export default ContentRowTop;